.l-seniorPage{
    display: grid;
    grid-template-columns: repeat(20, 1fr);
    &_sideNav{
        grid-column: 1/9;
        grid-row: 1/2;
    }
    &_content{
        grid-column: 6/21;
        grid-row: 1/2;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
    }
}